//加载相关依赖
import { mapState } from 'vuex';
import axios from 'axios'
import {urls} from '@Util/axiosConfig';
import technicianInfo from '@Components/technicianInfo'
export default {
  props: ['currentData'],
  data() {
    return {
      results: [],
      value: '',
      
      isInfoShow: false,
      isShowPop: false,
      menus: {
        menu1: '技师信息',
      },

      operObj: {
        number: ''
      },

      pulldownDefaultConfig: {
        content: '下拉刷新',
        height: 40,
        autoRefresh: false,
        downContent: '下拉刷新',
        upContent: '释放后刷新',
        loadingContent: '正在刷新...',
        clsPrefix: 'xs-plugin-pulldown-'
      },

      options3: ['A', 'B', 'C'],
      option5: 'B',

      currentInfo: {},

      listData: [],

    }
  },
  watch:{
    currentData(newVal,oldVal){
      if(newVal == 2){
        this.getTecList();
      }
    }
  },
  created(){
    let currentInfo = localStorage.getItem('currentInfo')
    this.currentInfo = JSON.parse(currentInfo)

    this.getTecList();
  },
  mounted(){

  },

  methods: {
    refresh() {
      this.getTecList()
    },

    // 获取技师列表
    getTecList(){
      let params = {
        holderId: this.currentInfo.holderId,
        holderGroup: this.currentInfo.holdGroup,
      }
      this.$ajaxPost(urls.GETTECHNICIANQUEUE, params).then(res => {
        if(res){
          this.listData = res.data
          setTimeout(()=>{
            this.$refs.scrollerBottom.donePulldown()
          }, 1000)
        }
      })
    },

    dealOper(oper){
      oper = Number(oper)
      // 1:排钟，2:选钟，3:加钟，4:点钟
      let name = '';
      switch (oper) {
        case 1 : name = '排'; break;
        case 2 : name = '选'; break;
        case 3 : name = '加'; break;
        case 4 : name = '点'; break;
      }
      return name
    },

    dealStatus(data){
      data = Number(data)
      // 1：暂停  2：空闲  3：上钟  4: 待上
      let name = '';
      switch (data) {
        case 1 : name = '暂停'; break;
        case 2 : name = '空闲'; break;
        case 3 : name = '上钟'; break;
        case 4 : name = '待上'; break;
        case 5 : name = '预约'; break;
      }
      return name
    },

    // 添加搜索功能
    setFocus () {
      this.$refs.search.setFocus()
    },
    resultClick (item) {
      window.alert('you click the result item: ' + JSON.stringify(item))
    },
    getResult (val) {
      console.log('on-change', val)
      this.results = val ? this.getResult(this.value) : []
    },
    onSubmit () {
      this.$refs.search.setBlur()
      this.$vux.toast.show({
        type: 'text',
        position: 'top',
        text: 'on submit'
      })
    },
    onFocus () {
      console.log('on focus')
    },
    onCancel () {
      console.log('on cancel')
    },

    // 单独点击时
    operItem(item){
      console.log(item)
      this.operObj = item
      this.isShowPop = true
    },

    dialogClose(){
      this.isInfoShow=false
    },

    showModal(){
      this.isInfoShow = true
    },

    closePop(){
      this.isShowPop = false        
    }
  },
  components: {
    technicianInfo
  }
}

